.footer_outer_container {
  display: flex;
  justify-content: center;
  background-color: #111;
  color: gray;
}
.footer_container {
  width: 60%;
}
.footer_icons {
  width: 15%;
  display: flex;
  justify-content: space-between;
  margin-left: 10px;
  color: white;
}
.footer_data {
  display: flex;
  justify-content: space-between;
}
.footer_data ul {
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 12vh;
}
.footer_data ul li {
  cursor: pointer;
}
.footer_service {
  display: flex;
  justify-content: center;
  align-items: center;
  border: #fff 1px solid;
  width: 20%;
  margin: 10px 0;
  cursor: pointer;
}
.copy-wright {
  font-size: 12px;
  margin-bottom: 10px;
}
